/*用户加床数达最大时，弹出的提示框的样式*/
html{
	body{
		.info-prompt {
			background-color: #000000;
			opacity: 0.5;
			filter: alpha(opacity=50);
			width: 100%;
			height: 100%;
			position: fixed;
			top: 0;
			left: 0;
			display: none;
		}
		.ui-dialog{
	  	    position: fixed;
	  	    display: block;
	        .ui-button-icon-only{/*去掉dialog默认的close字样*/
	      	    display: none;
	        }
		}
		.info-prompt-box {
			display: none;
			position: fixed;
			top: 280px;
			left: 570px;
		    width: 300px !important;/*不得已用important，因为dialog默认的样式很难覆盖*/
		    height: 150px !important;/*不得已用important，因为dialog默认的样式很难覆盖*/
		    background: #ffffff;
		    opacity: 1;
		    box-shadow: 1px 1px 50px rgba(0, 0, 0, .3);
		    .prompt-head {
				width: 100%;
				height: 40px;
				line-height: 40px;
				background-color: #33A6FF;
				color: #ffffff;
				font-size: 16px;
				span {
				   height: 100%;
				   line-height: 40px;
				   padding-left: 20px;
				}
				i {
				   padding-right: 20px;
				cursor: pointer;
				}
			}

			p {
				width: 100%;
				height: 70px;
				line-height: 70px;
				text-align: center;
				color: #000;
			}

			.max-bed-num-confirm {
				width: 100%;
				height: 40px;
				button {
					width: 50px;
					height: 30px;
					line-height: 30px;
					color: #ffffff;
					background-color: #33A6FF;
					border-radius: 5px;
					float: right;
					margin-right: 20px;
				}
			}
		}
	}    
}


